import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

const App = () => {
  return (
    <Router>
      <Link to="/">home</Link>
      <Link to="/another">another</Link>
      <Switch>
        <Route exact path="/" render={() => <div>Home Path</div>} />
        <Route exact path="/another" render={() => <div>Another Path</div>} />
      </Switch>
    </Router>
  );
};

export default App;